import banner from "../components/banner.vue"
export default {
  components: {
    banner
  },
  data() {
    return {
      indicatorDots: true,
      autoplay: true,
      interval: 2000,
      duration: 500,
      indicatorActiveColor: '#fff',
      searchInfo: '大家都在搜“森海塞尔”',
      scrollTop: 0,
      swiperList: [],
      menuList: [], // 所有商品分类列表
      discount: [
      {
        imgUrl: '../../static/img/market/pintuan-text.png',
        name: '拼团购',
        info: '拼得越多，越优惠',
        img: ['../../static/img/market/pintuan-icon.png']
      }, {
        imgUrl: '../../static/img/market/miaoshagou-text.png',
        name: '秒杀购',
        time: '',
        min_price: '',
        max_price: '',
        img: []
      }, {
        imgUrl: '../../static/img/market/xianshigou-text.png',
        name: '限时购',
        time: '',
        info: '',
        img: []
      }, {
        imgUrl: '../../static/img/market/zero-text.png',
        name: '0元购',
        time: '',
        min_price: '',
        max_price: '',
        img: []
      }],
      recommendList: []
    }
  },
  computed: {
    statusBarHeight() {
      return this.$store.state.statusBarHeight
    }
  },
  onLoad() {
    // 轮播图
    this.getSwiperList()
    // 获取所有商品分类
    this.getGoodscategory()
    
    // 获取推荐商品列表
    this.getRecommendgoods()
  },
  onShow() {
    // // 获取一个限时购商品
    // this.getLimitGoods()
    // 
    // // 获取一个秒杀商品
    // this.getKillGoods()
  },
  onPullDownRefresh() {
    console.log('refresh');
    setTimeout(function () {
        uni.stopPullDownRefresh();
    }, 1000);
  },
  methods: {
    
    pagetoTop() {
      console.log('点击了')
      this.scrollTop = 50
    },
    // 轮播图
    getSwiperList() {
      this.$http({
        url: this.$api.goods_gethomebanners,
        cb: (err, res) => {
          if(!err && res.code === 1) {
            console.log('首页轮播图数据', res.data)
            
            // 替换轮播图图片路径数据
            this.swiperList = res.data.list
          } else {
            uni.showToast({
            	title: '轮播图图片加载失败',
              icon: 'none'
            })
          }
        }
      })
    },
    
    // 获取所有商品分类
    getGoodscategory() {
      this.$http({
        url: this.$api.goodscategory,
        cb: (err, res) => {
          if (!err && res.code === 1) {
            this.menuList = res.data.list
          } else if (res.code === 0 && res.msg) {
            uni.showToast({
              title: res.msg,
              icon: 'none'
            })
          } else {
            uni.showToast({
              title: '商品分类列表加载失败',
              icon: 'none'
            })
          }
        }
      })
    },
    // 商城页展示一个限时抢购商品
    getLimitGoods() {
      this.$http({
        url: this.$api.getflashsalegoodsbyone,
        cb: (err, res) => {
          if (!err && res.code === 1) {
            console.log('限时', res.data)
            if(res.data.goods) {
              
            }
          } else if (res.code === 0 && res.msg) {
            uni.showToast({
              title: res.msg,
              icon: 'none'
            })
          } else {
            uni.showToast({
              title: '限时抢购商品加载失败',
              icon: 'none'
            })
          }
        }
      })
    },
    // 商城页展示一个秒杀商品
    getKillGoods() {
      this.$http({
        url: this.$api.getseckillgoodsbyone,
        cb: (err, res) => {
          if (!err && res.code === 1) {
            console.log('秒杀', res.data)
            if(res.data.goods) {
              
              
            } else {
              this.lightning[1] = ''
            }
          } else if (res.code === 0 && res.msg) {
            uni.showToast({
              title: res.msg,
              icon: 'none'
            })
          } else {
            uni.showToast({
              title: '秒杀抢购商品加载失败',
              icon: 'none'
            })
          }
        }
      })
    },
    // 推荐商品列表
    getRecommendgoods() {
      this.$http({
        url: this.$api.recommendgoods,
        cb: (err, res) => {
          if(!err && res.code === 1) {
            this.recommendList = res.data.list
          } else if(res.code === 0 && res.msg) {
            uni.showToast({
              title: res.msg,
              icon: 'none'
            })
          } else {
            uni.showToast({
              title: '推荐商品列表获取失败',
              icon: 'none'
            })
          }
        }
      })
    },
    // 去商品详情页
    goDetail(item) {
      console.log(item)
      uni.navigateTo({
        url: '../components/goodDetail?goods_id=' + item.goods_id
        // url: 'goodDetail' 
      })
    },
    // 抢购或者秒杀,零元购，拼团等
    goPintuan(index) {
      switch(index) {
        case 0:
          uni.showToast({
            title: '拼团暂未开放',
            icon: 'none'
          })
          break
        case 1:
          uni.navigateTo({
            url: 'panicBuy?origin=' + '秒杀'
          })
          break
        case 2:
          uni.navigateTo({
            url: 'panicBuy?origin=' + '限时购'
          })
          break
        case 3:
          uni.showToast({
            title: '零元购暂未开放',
            icon: 'none'
          })
          break
          // uni.navigateTo({
          //   url: 'zerodraw'
          // })
          // break
      }
    },
    // 搜索页
    goSearch() {
      uni.navigateTo({
        url: '../components/search?type=1'
      })
    },
    // 商品分类
    goGoods(id, name) {
      if(name !== 'MORE') {
        uni.navigateTo({
          url: '../components/goods?id=' + id
        })
        return
      }
      uni.navigateTo({
        url: '../moreList/moreList'
      })
      
    },
    // 点击更多
    goMore() {
      uni.navigateTo({
        url: "../moreList/moreList"
      })
    }
  }
}
